import {
    MDBNavbar,
    MDBNavbarBrand,
    MDBNavbarNav,
    MDBNavbarToggler,
    MDBCollapse,
    MDBNavItem,
    MDBNavLink
} from "mdbreact";
import React, {Component} from 'react';

export default class Navigation extends Component {
    state = {
        collapseID: ""
    };

    toggleCollapse = collapseID => () =>
        this.setState(prevState => ({
            collapseID: prevState.collapseID !== collapseID ? collapseID : ""
        }));

    closeCollapse = collapseID => () => {
        window.scrollTo(0, 0);
        this.state.collapseID === collapseID && this.setState({ collapseID: "" });
    };
    render() {

        const overlay = (
            <div
                id="sidenav-overlay"
                style={{ backgroundColor: "transparent" }}
                onClick={this.toggleCollapse("mainNavbarCollapse")}
            />
        );

        const { collapseID } = this.state;

        return (
            <>
              <MDBNavbar color="indigo" dark expand="md" scrolling fixed="top">
                <MDBNavbarBrand href="/" className="py-0 font-weight-bold">
                  <i className="fas fa-school" style={{ height: "1.5rem", width: "2rem", paddingRight: "10px" }}></i>
                  <strong className="align-middle">MODREG</strong>
                </MDBNavbarBrand>
                <MDBNavbarToggler
                  onClick={this.toggleCollapse("mainNavbarCollapse")}
                />
                <MDBCollapse
                  id="mainNavbarCollapse"
                  isOpen={this.state.collapseID}
                  navbar
                >
                  <MDBNavbarNav right>
                    <MDBNavItem style={{ paddingRight: 10 }}>
                      <MDBNavLink
                        exact
                        to="/"
                      >
                        <strong>Home</strong>
                      </MDBNavLink>
                    </MDBNavItem>
                      <MDBNavItem style={{ paddingRight: 10 }}>
                        <MDBNavLink
                          to={`/student`}
                        >
                          <strong>My Classes</strong>
                        </MDBNavLink>
                      </MDBNavItem>
                  </MDBNavbarNav>
                </MDBCollapse>
              </MDBNavbar>
              {collapseID && overlay}
            </>
        )
    }
}